// Full updated GERCAB-GO code with all features combined into one app

"use client";
import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";
import {
  Card,
  CardContent,
} from "@/components/ui/card";
import * as XLSX from "xlsx";

interface TantanganItem {
  nama: string;
  deskripsi: string;
  deadline: string;
}

interface InspirasiItem {
  kutipan: string;
  sumber: string;
}

interface LaporanItem {
  pelapor: string;
  lokasi: string;
  deskripsi: string;
  tanggal: string;
}

interface GaleriItem {
  lokasi: string;
  jenis: string;
  bentuk: string;
  waktu: string;
}

interface AgendaItem {
  judul: string;
  tanggal: string;
  keterangan: string;
}

export default function GercabGo() {
  const [tantangan, setTantangan] = useState<TantanganItem[]>([]);
  const [inspirasi, setInspirasi] = useState<InspirasiItem[]>([]);
  const [laporan, setLaporan] = useState<LaporanItem[]>([]);
  const [galeri, setGaleri] = useState<GaleriItem[]>([]);
  const [agenda, setAgenda] = useState<AgendaItem[]>([]);

  const [namaTantangan, setNamaTantangan] = useState("");
  const [deskripsiTantangan, setDeskripsiTantangan] = useState("");
  const [deadlineTantangan, setDeadlineTantangan] = useState("");

  const [kutipan, setKutipan] = useState("");
  const [sumber, setSumber] = useState("");

  const [pelapor, setPelapor] = useState("");
  const [lokasiLapor, setLokasiLapor] = useState("");
  const [deskripsiLapor, setDeskripsiLapor] = useState("");
  const [tanggalLapor, setTanggalLapor] = useState("");

  const [lokasiGaleri, setLokasiGaleri] = useState("");
  const [jenisGaleri, setJenisGaleri] = useState("");
  const [bentukGaleri, setBentukGaleri] = useState("");
  const [waktuGaleri, setWaktuGaleri] = useState("");

  const [judulAgenda, setJudulAgenda] = useState("");
  const [tanggalAgenda, setTanggalAgenda] = useState("");
  const [keteranganAgenda, setKeteranganAgenda] = useState("");

  const exportExcel = (data: any[], filename: string) => {
    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Data");
    XLSX.writeFile(workbook, `${filename}.xlsx`);
  };

  return (
    <div className="p-8">
      <Tabs defaultValue="tantangan">
        <TabsList className="grid grid-cols-5 w-full mb-4">
          <TabsTrigger value="tantangan">Tantangan</TabsTrigger>
          <TabsTrigger value="inspirasi">Inspirasi</TabsTrigger>
          <TabsTrigger value="lapor">Lapor</TabsTrigger>
          <TabsTrigger value="galeri">Galeri</TabsTrigger>
          <TabsTrigger value="agenda">Agenda</TabsTrigger>
        </TabsList>

        {/* Tantangan */}
        <TabsContent value="tantangan">
          <h2 className="text-xl font-bold mb-4">Tambah Tantangan</h2>
          <Input placeholder="Nama" value={namaTantangan} onChange={(e) => setNamaTantangan(e.target.value)} />
          <Input placeholder="Deskripsi" value={deskripsiTantangan} onChange={(e) => setDeskripsiTantangan(e.target.value)} />
          <Input placeholder="Deadline" value={deadlineTantangan} onChange={(e) => setDeadlineTantangan(e.target.value)} />
          <Button className="my-2" onClick={() => {
            setTantangan([...tantangan, { nama: namaTantangan, deskripsi: deskripsiTantangan, deadline: deadlineTantangan }]);
            setNamaTantangan(""); setDeskripsiTantangan(""); setDeadlineTantangan("");
          }}>Tambah</Button>
          <Button variant="outline" onClick={() => exportExcel(tantangan, "Tantangan")}>Download Excel</Button>
          {tantangan.map((t, i) => <Card key={i}><CardContent className="p-2">{t.nama} - {t.deskripsi} - {t.deadline}</CardContent></Card>)}
        </TabsContent>

        {/* Inspirasi */}
        <TabsContent value="inspirasi">
          <h2 className="text-xl font-bold mb-4">Tambah Inspirasi Hijau</h2>
          <Input placeholder="Kutipan" value={kutipan} onChange={(e) => setKutipan(e.target.value)} />
          <Input placeholder="Sumber" value={sumber} onChange={(e) => setSumber(e.target.value)} />
          <Button className="my-2" onClick={() => {
            setInspirasi([...inspirasi, { kutipan, sumber }]);
            setKutipan(""); setSumber("");
          }}>Tambah</Button>
          <Button variant="outline" onClick={() => exportExcel(inspirasi, "Inspirasi")}>Download Excel</Button>
          {inspirasi.map((i, x) => <Card key={x}><CardContent className="p-2">"{i.kutipan}" - {i.sumber}</CardContent></Card>)}
        </TabsContent>

        {/* Lapor */}
        <TabsContent value="lapor">
          <h2 className="text-xl font-bold mb-4">Lapor Lingkungan</h2>
          <Input placeholder="Pelapor" value={pelapor} onChange={(e) => setPelapor(e.target.value)} />
          <Input placeholder="Lokasi" value={lokasiLapor} onChange={(e) => setLokasiLapor(e.target.value)} />
          <Input placeholder="Deskripsi" value={deskripsiLapor} onChange={(e) => setDeskripsiLapor(e.target.value)} />
          <Input placeholder="Tanggal" value={tanggalLapor} onChange={(e) => setTanggalLapor(e.target.value)} />
          <Button className="my-2" onClick={() => {
            setLaporan([...laporan, { pelapor, lokasi: lokasiLapor, deskripsi: deskripsiLapor, tanggal: tanggalLapor }]);
            setPelapor(""); setLokasiLapor(""); setDeskripsiLapor(""); setTanggalLapor("");
          }}>Tambah</Button>
          <Button variant="outline" onClick={() => exportExcel(laporan, "Laporan")}>Download Excel</Button>
          {laporan.map((l, i) => <Card key={i}><CardContent className="p-2">{l.pelapor} - {l.lokasi} - {l.deskripsi} - {l.tanggal}</CardContent></Card>)}
        </TabsContent>

        {/* Galeri */}
        <TabsContent value="galeri">
          <h2 className="text-xl font-bold mb-4">Galeri Kearifan Lokal</h2>
          <Input placeholder="Lokasi" value={lokasiGaleri} onChange={(e) => setLokasiGaleri(e.target.value)} />
          <Input placeholder="Jenis" value={jenisGaleri} onChange={(e) => setJenisGaleri(e.target.value)} />
          <Input placeholder="Bentuk" value={bentukGaleri} onChange={(e) => setBentukGaleri(e.target.value)} />
          <Input placeholder="Waktu" value={waktuGaleri} onChange={(e) => setWaktuGaleri(e.target.value)} />
          <Button className="my-2" onClick={() => {
            setGaleri([...galeri, { lokasi: lokasiGaleri, jenis: jenisGaleri, bentuk: bentukGaleri, waktu: waktuGaleri }]);
            setLokasiGaleri(""); setJenisGaleri(""); setBentukGaleri(""); setWaktuGaleri("");
          }}>Tambah</Button>
          <Button variant="outline" onClick={() => exportExcel(galeri, "Galeri")}>Download Excel</Button>
          {galeri.map((g, i) => <Card key={i}><CardContent className="p-2">{g.lokasi} - {g.jenis} - {g.bentuk} - {g.waktu}</CardContent></Card>)}
        </TabsContent>

        {/* Agenda */}
        <TabsContent value="agenda">
          <h2 className="text-xl font-bold mb-4">Agenda Kegiatan</h2>
          <Input placeholder="Judul" value={judulAgenda} onChange={(e) => setJudulAgenda(e.target.value)} />
          <Input placeholder="Tanggal" value={tanggalAgenda} onChange={(e) => setTanggalAgenda(e.target.value)} />
          <Input placeholder="Keterangan" value={keteranganAgenda} onChange={(e) => setKeteranganAgenda(e.target.value)} />
          <Button className="my-2" onClick={() => {
            setAgenda([...agenda, { judul: judulAgenda, tanggal: tanggalAgenda, keterangan: keteranganAgenda }]);
            setJudulAgenda(""); setTanggalAgenda(""); setKeteranganAgenda("");
          }}>Tambah</Button>
          <Button variant="outline" onClick={() => exportExcel(agenda, "Agenda")}>Download Excel</Button>
          {agenda.map((a, i) => <Card key={i}><CardContent className="p-2">{a.judul} - {a.tanggal} - {a.keterangan}</CardContent></Card>)}
        </TabsContent>
      </Tabs>
    </div>
  );
}
